body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{font-family:"Lora",serif;color:#343a40;font-size:medium}.main{background-image:url(/typescript-portfolio/static/media/background.aec576f9.jpg);background-repeat:no-repeat;background-position:0 0;background-position:initial;background-color:#686369;height:100vh;display:flex;flex-direction:column;align-items:center}
*{font-family:"Lora",serif;color:#343a40;font-size:medium}.projects{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#f1f0ea;border:1px #f1f0ea;border-radius:5px;padding:10px;margin:50px;width:80%}.projects__Header{display:flex;justify-content:space-between;width:100%}.projects__Arrow{display:block;height:30px;margin:10px}.projects__Content{display:flex}.projects__Content--Image{margin:10px;height:200px}@media all and (max-width: 500px){.projects{display:inline-block}.projects__Content--Image{display:none}}
*{font-family:"Lora",serif;color:#343a40;font-size:medium}.profileCard{flex-flow:row nowrap;background-color:#f1f0ea;border:1px #f1f0ea;border-radius:5px;padding:10px;margin:50px;width:80%}.profileCard__Content{display:flex}.profileCard__Text{display:block}.profileCard__Links{display:flex;justify-content:space-around}.profileCard__Links--Images{height:50px}.profileCard__Header{display:block}.profileCard__Content--Picture{margin:10px;height:200px}@media all and (max-width: 500px){.profileCard{display:inline-block}.profileCard__Content--Picture{display:none}}
*{font-family:"Lora",serif;color:#343a40;font-size:medium}.techStack{flex-flow:row nowrap;background-color:#f1f0ea;border:1px #f1f0ea;border-radius:5px;padding:10px;margin:50px;width:80%}.techStack__Content{display:flex;justify-content:space-around}.techStack__Images{margin:10px;height:50px}@media all and (max-width: 500px){section{display:inline-block}}
